.popup-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    overflow-y: auto;
}

.popup-body {
    position: relative;
    width: 100%;
    max-width: 900px;
    max-height: 90vh;
    min-height: auto;
    overflow-y: auto;
    border-radius: 12px;
    border: 1px solid #0e7490;
    background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
    box-shadow: 0 25px 50px rgba(6, 182, 212, 0.2);
    color: #f1f5f9;
    padding-bottom: 20px;
}